.users {
    width: 100%;
    height: 500px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background-color: var(--wrap-bg-color);
}


.search {
    height: 160px;
    padding: 30px;
    border-radius: 10px;
    background-color: var(--wrap-bg-color);
    margin-bottom: 20px;
}

.search h3 {
    font-size: 20px;
    font-weight: 600;
    color: var(--dark-blue-w-text-color);
    margin-bottom: 20px;
}

.notFound {
    color: var(--b-w-text-color);
    margin-top: 10px;
}

.usersBlock {
    margin-top: 10px;
}

.userContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    padding: 15px 30px;
    height: 130px;
    border-radius: 10px;

    margin-bottom: 20px;
    gap: 20px;
}

@media screen and (max-width: 678px) {
    .users {
        width: 100%;
        height: 1550px;
    }
}

.user {
    display: flex;
    align-items: center;
    margin-top: 40px;

}

.photo {
    width: 100px;
    height: 100px;
    margin-right: 20px;
    border-radius: 60px;
    border: solid 2px var(--female-color);
}

.malePhoto {
    border: solid 2px var(--male-color);
}

.fol {
    justify-self: right;
}

.descriptionUser {
    justify-self: left;
    color: var(--dark-blue-w-text-color);
    width: 100%;
}

.name {
    font-size: 15px;
    font-weight: bold;
    color: var(--b-w-text-color);
}

.paginator {
    margin-bottom: 16px;
}


@media screen and (max-width: 900px) {
    .usersBlock {
        margin-top: 5px;
    }

    .search {
        margin-bottom: 10px;
    }

    .userContainer {
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 600px) {
    .userContainer {
        padding: 8px 1.5%;
        height: 100px;
        margin-bottom: 5px;
    }

    .paginator {

    }

    .photo {
        width: 85px;
        height: 85px;
    }

    .name {
        font-size: 16px;
        font-weight: bold;
    }

    .search {
        margin-bottom: 0;
        padding: 10px 10px;
        height: 120px;
    }
}
