.navbar {
    height: fit-content;
    border-radius: 10px;
    background-color: var(--wrap-bg-color);
    width: 280px;
    margin-right: 20px;
    padding: 0 40px 40px;

}
@media screen and (max-width: 678px) {
   .navbar{
       border-radius: 0;
       margin: 0;
       padding: 0;
       height: 51px;
       bottom: 0;
       left: 0;
       z-index: 5;
       width: 100vw;
       background-color: var(--head-bg-color);
       border-top: 1px var(--mininav-border-color) solid;
       position: fixed;
       display: flex;
       flex-direction: row-reverse;
       justify-content: space-around;
       align-items: center;
   }
}

.link {
    display: flex;
    margin-top: 40px;
    align-items: center;
    min-width: 100px;

}
@media screen and (max-width: 678px) {
   .link {
       min-width: 20px;
       margin-top: 0;
   }
}
.link svg {
    width: 18px;
    height: 18px;
    margin-right: 5px;
    fill: #9d9aa8;
}

.item {
    max-width: 150px;
    height: 18px;
    font-size: 14px;
    font-weight: 500;
    color: #9d9aa8;
/**/
}
@media screen and (max-width: 678px) {
   .item {
       display: none;
   }
}
.link:hover .item,
.activeItem .item {
    color: var(--secondary-color)
}

.link:hover svg,
.activeItem svg {
    fill: var(--secondary-color)
}

.itemLogout svg,
.itemLogout .item {
    cursor: pointer;
    fill: #f81c1c;
    color: #f81c1c;
}

.activeLi svg {
    fill: var(--secondary-color)
}


@media screen and (max-width: 678px) {
    .hideOnMobile {
        display: none;
    }
}
.messagesCount {
    width: 16px;
    height: 16px;
    border-radius: 25px;
    background-color: #f00;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    margin: 5px 0 0 4px;
}